/* Prevents img without src to appear */
img.lazy {
  opacity: 0;
}
img.lazy:not(.initial) {
  transition: opacity 1s;
}
img.lazy.initial, img.lazy.loaded, img.lazy.error {
  opacity: 1;
}

img:not([src]) {
  visibility: hidden;
}

